<template>
  <v-btn 
    color="green"
    dark
    @click="getWord(profiles)"
  >
    .docx
  </v-btn>
</template>
<script>
import { mapGetters } from 'vuex'
import { 
  AlignmentType,
  Document, 
  Media, 
  Header,
  HeadingLevel,
  Paragraph, 
  Packer, 
  TextRun, 
  TableOfContents, 
  Table,
  TableRow,
  TableCell,
  TableAnchorType,
  RelativeHorizontalPosition,
  RelativeVerticalPosition,
  TableLayoutType,
  StyleLevel, 
} from "docx"
import { saveAs } from 'file-saver'
import moment from 'moment'
// import fs from 'fs'
export default {
  name: 'VaucherWord',
  props: {
    profiles: {
      type: Object,
      default: () => {
        return {}
      } 
    },
    tour: {
      type: Object,
      default: () => {
        return {}
      } 
    },
    order: {
      type: Object,
      default: () => {
        return {}
      } 
    }
  },
  data() {
    return {
      
    }
  },
  computed: {
    ...mapGetters([
      'getOrderContacts',
    ])
  },
  mounted() {
    console.log(this.profiles, JSON.parse(this.tour.extra), this.order)
  },
  methods: {
    getWord(order) {
      const doc = new Document();
      doc.addSection({
        headers: {
          default: new Header({
            children: [
              new Paragraph("   www.alfavit-travel.ru"),
              new Paragraph(" 7840055086 /  784001001"),
              new Paragraph("191014, . -,  , 1, . , . 132"),
              new Paragraph({
                border: {
                  bottom: {
                    color: "auto",
                    space: 1,
                    value: "single",
                    size: 6,
                  },
                },
                text: ".: +7 (812) 579-65-56 info@alfavit-travel.ru",
              }),
            ]
          }),
        },
        children: [
          new Paragraph({
            text: " No 18/07/19",
            alignment: AlignmentType.CENTER,
            heading: HeadingLevel.HEADING_1,
          }),
        ],
        children: this.getData(),
      })
      Packer.toBlob(doc).then((blob) => {
        saveAs(blob, "word.docx");
      });
    },
    getData() {
      let result = []
      // vaucher number
      result.push(
        new Paragraph({
        text: " No 18/07/19",
        alignment: AlignmentType.CENTER,
        heading: HeadingLevel.HEADING_1,
      }))
      // tourist info
      result.push(...this.touristsInfo())
      // tour info
      result.push(...this.tourInfo())
      return result
    },
    touristsInfo() {
      let result = []
      let profilesCount = 0
      result.push(new Paragraph({
        text: ``,
        alignment: AlignmentType.CENTER,
        heading: HeadingLevel.HEADING_2,
      }))
      for (let key in this.profiles) {
        const profile = this.profiles[key]
        profilesCount += 1
        result.push(
          new Paragraph({
            children: [
              new TextRun({
                text: ` ${parseInt(key) + 1}: `,
                bold: true,
                underline: {},
              })
            ]
          }),
          new Paragraph({
            text: ` : ${profile.first_name} ${profile.last_name}`,
          }),
          new Paragraph({
            text: ` : ${profile.dob}`,
          }),
          new Paragraph({
            text: `: ${profile.passport}`,
          }),
        )
      }
      result.push(
        new Paragraph({
          text: ` : ${this.getOrderContacts.email} ${this.getOrderContacts.phone} ${this.getOrderContacts.name}`,
        })
      )
      result.push(
        new Paragraph({
          text: ` : ${profilesCount}`,
          border: {
            bottom: {
              color: "auto",
              space: 1,
              value: "single",
              size: 6,
            },
          },
        })
      )
      return result
    },
    tourInfo() {
      const tour = JSON.parse(this.tour.extra)
      let result = []
      moment.locale('ru')
      const dateStart = moment(tour.options.dateStart).format('LL')
      result.push(new Paragraph({
        text: `: "${tour.options.name}"`,
        alignment: AlignmentType.CENTER,
        heading: HeadingLevel.HEADING_2,
      }))
      result.push(
        new Paragraph({
          children: [
            new TextRun({
              text: ` : ${dateStart}. `,
            }),
            new TextRun({
              text: ` : ${tour.options.days}.`,
            }),
          ],
          border: {
            bottom: {
              color: "auto",
              space: 1,
              value: "single",
              size: 6,
            },
          },
        }),
      )
      result.push(new Paragraph({
        text: `"`,
        alignment: AlignmentType.CENTER,
        heading: HeadingLevel.HEADING_3,
      }))
      tour.hotel.forEach((hotel) => {
        result.push(
          new Paragraph({
          children: [
            new TextRun({
              text: `: ${hotel.hotel.name}. `,
            }),
            new TextRun({
              text: `: ${hotel.obj.name}. `,
            }),
            new TextRun({
              text: ` : 1.`,
            }),
          ],
          border: {
            bottom: {
              color: "auto",
              space: 1,
              value: "single",
              size: 6,
            },
          },
        }))
      })
      result.push(new Paragraph({
        text: `"`,
        alignment: AlignmentType.CENTER,
        heading: HeadingLevel.HEADING_3,
      }))
      tour.museum.forEach((museum) => {
        result.push(
          new Paragraph({
          children: [
            new TextRun({
              text: `${museum.museum.name}, `,
            }),
            new TextRun({
              text: `${museum.obj.name}.`,
            }),
          ],
          border: {
            bottom: {
              color: "auto",
              space: 1,
              value: "single",
              size: 6,
            },
          },
        }))
      })
      result.push(new Paragraph({
        text: `"`,
        alignment: AlignmentType.CENTER,
        heading: HeadingLevel.HEADING_3,
      }))
      tour.meal.forEach((meal) => {
        result.push(
          new Paragraph({
          children: [
            new TextRun({
              text: `: ${meal.obj.day}: ${meal.meal.name}, `,
            }),
            new TextRun({
              text: `${meal.obj.name}, ${meal.obj.description}.`,
            }),
          ],
          border: {
            bottom: {
              color: "auto",
              space: 1,
              value: "single",
              size: 6,
            },
          },
        }))
      })
      result.push(
        new Paragraph({
          text: ` : ${this.order.total_price}.`,
        })
      )
      result.push(
          new Paragraph({
            text: ` :  .. : 8-911-222-12-32`,
        })
      )
      console.log(result)
      return result
    }
  }
}
</script>